"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Loader2, Download, CheckCircle, AlertCircle } from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { useAuth } from "@/lib/auth";

interface GeneratedFile {
  filename: string;
  content: string;
  path: string;
}

interface ImportResult {
  success: boolean;
  message: string;
  files?: GeneratedFile[];
  slug?: string;
}

const outputFormats = [
  { id: "react", label: "React" },
  { id: "nextjs", label: "Next.js" },
  { id: "vue", label: "Vue" },
];

export default function FigmaImport() {
  const { user } = useAuth();
  const [figmaUrl, setFigmaUrl] = useState("");
  const [componentName, setComponentName] = useState("");
  const [outputFormat, setOutputFormat] = useState("react");
  const [useTailwind, setUseTailwind] = useState(true);
  const [includeStorybook, setIncludeStorybook] = useState(true);
  const [isImporting, setIsImporting] = useState(false);
  const [result, setResult] = useState<ImportResult | null>(null);
  const [error, setError] = useState("");
  const [hasCredits, setHasCredits] = useState(false);

  useEffect(() => {
    // Simulate checking user credits and login status
    if (user) {
      // For demo, assume user has credits
      setHasCredits(true);
    } else {
      setHasCredits(false);
    }
  }, [user]);

  const extractFigmaInfo = (url: string) => {
    const fileMatch = url.match(/figma\.com\/file\/([a-zA-Z0-9]+)/);
    const nodeMatch = url.match(/node-id=([^&]+)/);
    return {
      fileId: fileMatch ? fileMatch[1] : null,
      nodeId: nodeMatch ? decodeURIComponent(nodeMatch[1]) : null,
    };
  };

  const handleImport = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setResult(null);

    if (!user) {
      setError("You must be logged in to use this feature.");
      return;
    }

    if (!hasCredits) {
      setError("Insufficient credits. Please upgrade your account.");
      return;
    }

    setIsImporting(true);

    try {
      const { fileId, nodeId } = extractFigmaInfo(figmaUrl);

      if (!fileId) {
        throw new Error("Invalid Figma URL. Please provide a valid design link.");
      }

      // Simulate import delay and API call
      await new Promise(resolve => setTimeout(resolve, 2000));

      // Simulate generated files
      const slug = componentName.toLowerCase().replace(/[^a-z0-9]/g, '-') + '-' + Date.now();

      const generatedFiles: GeneratedFile[] = [
        {
          filename: `${componentName || "Component"}.tsx`,
          content: "// React component code here",
          path: `/generated/${slug}/${componentName || "Component"}.tsx`,
        },
        {
          filename: `${componentName || "Component"}.types.ts`,
          content: "// TypeScript types here",
          path: `/generated/${slug}/${componentName || "Component"}.types.ts`,
        },
        {
          filename: `${componentName || "Component"}.stories.tsx`,
          content: "// Storybook stories here",
          path: `/generated/${slug}/${componentName || "Component"}.stories.tsx`,
        },
        {
          filename: "README.md",
          content: `# ${componentName || "Component"}\n\nGenerated from Figma design.\n\nOutput format: ${outputFormat}\nTailwind CSS: ${useTailwind ? "Yes" : "No"}\nStorybook: ${includeStorybook ? "Included" : "Excluded"}`,
          path: `/generated/${slug}/README.md`,
        },
      ];

      setResult({
        success: true,
        message: "Component generated successfully.",
        files: generatedFiles,
        slug,
      });
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred during import.");
    } finally {
      setIsImporting(false);
    }
  };

  const downloadZip = () => {
    // For demo, just alert
    alert("Download ZIP functionality is not implemented in this demo.");
  };

  return (
    <div className="flex-1 space-y-6 p-4 md:p-6">
      <div className="flex items-center gap-2">
        <CheckCircle className="w-6 h-6 text-[#FF5C5C]" />
        <h1 className="text-3xl font-bold tracking-tight">Import from Figma (Premium)</h1>
      </div>
      <p className="text-gray-600">Import Figma designs and generate components. Available for logged-in users with credits.</p>

      <Card>
        <CardHeader>
          <CardTitle>Figma Design Import</CardTitle>
          <CardDescription>Paste your Figma design link and configure output options</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleImport} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="figmaUrl">Figma Design URL</Label>
              <Input
                id="figmaUrl"
                placeholder="https://www.figma.com/file/abc123/Design?node-id=1%3A2"
                value={figmaUrl}
                onChange={(e) => setFigmaUrl(e.target.value)}
                required
                disabled={isImporting}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="componentName">Component Name</Label>
              <Input
                id="componentName"
                placeholder="e.g., HeroSection"
                value={componentName}
                onChange={(e) => setComponentName(e.target.value)}
                disabled={isImporting}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="outputFormat">Output Format</Label>
              <Select
                value={outputFormat}
                onValueChange={(value) => setOutputFormat(value)}
                disabled={isImporting}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select output format" />
                </SelectTrigger>
                <SelectContent>
                  {outputFormats.map((format) => (
                    <SelectItem key={format.id} value={format.id}>
                      {format.label}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="flex items-center space-x-4">
              <label className="inline-flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={useTailwind}
                  onChange={() => setUseTailwind(!useTailwind)}
                  disabled={isImporting}
                />
                <span>Use Tailwind CSS</span>
              </label>

              <label className="inline-flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={includeStorybook}
                  onChange={() => setIncludeStorybook(!includeStorybook)}
                  disabled={isImporting}
                />
                <span>Include Storybook Stories</span>
              </label>
            </div>

            {error && (
              <Alert variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <Button
              type="submit"
              className="w-full bg-[#FF5C5C] hover:bg-[#E54545]"
              disabled={isImporting}
            >
              {isImporting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Import in progress...
                </>
              ) : (
                "Import Design"
              )}
            </Button>
          </form>
        </CardContent>
      </Card>

      {result && (
        <Card>
          <CardHeader>
            <CardTitle>Import Results</CardTitle>
            <CardDescription>{result.message}</CardDescription>
          </CardHeader>
          <CardContent>
            <ul className="list-disc list-inside space-y-1">
              {result.files?.map((file, index) => (
                <li key={index}>
                  <span className="font-mono">{file.filename}</span> - <span className="text-sm text-gray-600">{file.path}</span>
                </li>
              ))}
            </ul>
            <Button className="mt-4" onClick={downloadZip}>
              <Download className="inline-block mr-2 w-4 h-4" />
              Download ZIP
            </Button>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
